/*分类*/
.teach {
    width: 100%;
    height: 185px;
    border-bottom: 1px solid #e7e6e8;
}
.teach .title{
    height: 50px;
    line-height: 50px;
    font-size: 13px;
    color: #999;
    margin: 0 15px;
}
.teach .title .text{
    float: left;
}
.teach .title .more{
    float: right;
    font-size: 16px;
}
.teach .grade a{
    text-decoration: none;
    float: left;
    width: 107px;
    height: 30px;
    line-height: 30px;
    margin:0 15px 15px;
    color: #666;
    text-align: center;
    background: #f4f4f4;
    border-radius: 3px;
    font-size: 13px;
}
.box{
    height: 50px;
